import QR from "../../utils/wxqrcode.js"

export default {
	data() {
		return {
			card: '',
			img:''
		}
	},
	onLoad() {
		// 隐藏系统tabBar
		// uni.hideTabBar();
		// 绘制新的tabbar
		// app.editTabbar();
		var that = this;
		that.creatQRcode();
	},
	onReady() {
		
	},
	methods: {
		creatQRcode: function() {
			var that = this;
			uni.getStorage({
				key: 'card',
				success: function(res) {
					var card = res.data;
					that.card = card;
					// var qrcode = new QRCode('canvas', {
					// 	text: card,
					// 	width: 260,
					// 	height: 260,
					// 	colorDark: "#000000",
					// 	colorLight: "#ffffff",
					// 	correctLevel: QRCode.CorrectLevel.H,
					// });
					let img = QR.createQrCodeImg(card, {  
					     size: parseInt(300)//二维码大小  
					})
					that.img = img;
					console.log(img)
					//console.log(token);
				},
				fail: function(err) {
					console.log(err);
					that.$util.showToast(err.errMsg, 'none', 3000);
					uni.redirectTo({
						url: '../login/login'
					});
				}
			})
		}
	}
}
